<template>
  <info-box title="我要预约">
    <a-form :form="form" @submit.prevent="handleSubmit">
      <a-form-item label="请选择日期">
        <a-radio-group :defaultValue="week[0]" buttonStyle="solid">
          <a-radio-button
            v-for="item in week"
            :key="item.getTime()"
            :value="item"
          >{{item | date('MM-dd')}}/{{item | week}}</a-radio-button>
        </a-radio-group>
      </a-form-item>
      <a-form-item label="请选择时间段">
        <a-radio-group :defaultValue="times[0]" buttonStyle="solid">
          <a-radio-button v-for="item in times" :key="item" :value="item">{{item}}</a-radio-button>
        </a-radio-group>
      </a-form-item>
      <a-form-item label="请选择服务">
        <a-select defaultValue="a">
          <a-select-option value="a">打印</a-select-option>
          <a-select-option value="b">打印&上色</a-select-option>
        </a-select>
      </a-form-item>
      <a-form-item label="备注">
        <a-textarea placeholder="请输入备注" :autosize="{ minRows: 2, maxRows: 6 }"/>
      </a-form-item>
      <a-form-item>
        <a-button type="primary" html-type="submit">预约</a-button>
      </a-form-item>
    </a-form>
  </info-box>
</template>

<script>
import InfoBox from './InfoBox.vue'

export default {
  components: { InfoBox },
  data () {
    const week = []
    const NOW = new Date()
    let year = NOW.getFullYear()
    let month = NOW.getMonth()
    let date = NOW.getDate()
    for (let _i = 0; _i < 7; _i++) {
      week.push(new Date(year, month, ++date))
    }
    return {
      week,
      times: ['9:00~10:00', '10:00~11:00', '13:00~14:00', '14:00~15:00', '15:00~16:00', '16:00~17:00']
    }
  },
  methods: {
    handleSubmit () {
      this.form.validateFields((err, values) => {
        if (!err) {
          console.log('Received values of form: ', values)
        }
      })
    }
  }
}
</script>
